.homepage {

    .banner-container{
      display: flex;
      width: 100%;
      position: relative;
      height: 650px;
      font-family: "Open Sans Condensed";
      flex-direction: column;
      justify-content: center;
      
      .banner-image{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0%;
        z-index: -1;

      }
      .banner-title, .banner-description{
        padding-left: 40px;
        @media screen and (max-width: 800px){
          padding-left: unset;
      }
        
      }

      .banner-title {
        color: hotpink;
        font-size: 95px;
      }
      .banner-description{
        font-size: 45px;
        color: hotpink;
        bottom: auto;
      }
    }
    @media screen and (max-width: 800px){
      text-align: center;
  }

}
  
  
  
  