.collection-page {
    display: flex;
    flex-direction: column;

    .title {
        font-size: 28px;
        margin: 0 auto 30px;
    }

    .items {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        grid-gap: 10px;
        @media screen and (max-width: 800px){
            grid-template-columns: 1fr;
            grid-gap: 15px;
        }
    }
}