.collection-preview {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    
    
    .title {
        font-size: 28px;
        margin-bottom: 25px;
    }

    .preview {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        grid-gap: 10px;
        @media screen and (max-width: 800px) {
            grid-template-columns: 1fr;
            grid-gap: 15px;
            
        }
    }
    @media screen and (max-width: 800px) {
        align-items: center;
    }
}