.checkout-item {
    width: 100%;
    display: flex;
    min-height: 100px;
    border-bottom: 1px solid darkgrey;
    padding: 15px 0;
    font-size: 20px;
    align-items: center;

    .image-container {
        width: 23%;
        padding-right: 15px;
        position: relative;

        img {
            width: 100%;
            height: 100%;
        }
    }
    .name,
    .quantity,
    .price {
        width: 23%;
    }

    .quantity {
        display: flex;
        align-items: center;

        .arrow {
            cursor: pointer;
        }

        .value {
            // font-family: 'Open Sans Condensed';
            margin: 0 10px;
           top: 10px;
        }
    }
    .remove-button {
        padding-left: 12px;
        cursor: pointer;
      }
}