.checkout-page {
    width: 55%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px auto 0;

    .checkout-header{
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid darkgrey;

        .header-block {
            text-transform: capitalize;
            width: 23%;

            &:last-child {
                width: 8%;
            }   
        }  
    }
    .total {
        margin-top: 30px;
        margin-left: auto;
        font-size: 36px;
        @media screen and (max-width: 800px){
            margin: 30px auto;
        }
      }
    @media screen and (max-width: 800px){
        width: 90vw;
    }
}